<template>
  <b-container fluid>
    <b-form>
      <b-row class="mt-2">
        <b-col sm="2" offset="2">

          <b-input v-model="marka" v-model.trim="$v.marka.$model" class="mb-2 mr-sm-2 mb-sm-0 " :class="{ 'is-invalid': $v.marka.$error , 'is-valid': !$v.marka.$invalid }" placeholder="Marka telefona"></b-input>
          <div class="valid-feedback">Uneli ste ispravan podatak</div>
          <div class="invalid-feedback">
            <p v-if="!$v.marka.required">Polje marka mora biti popunjeno</p>
            <p v-if="!$v.marka.minLength">Morate imati najmanje 3 karaktera</p>
            <p v-if="!$v.marka.minLength">Morate imate najvise 15 karaktera</p>
          </div>

        </b-col>
        <b-col sm="5">
          <b-form-textarea  v-model="model"  v-model.trim="$v.model.$model" :class="{ 'is-invalid':$v.model.$error , 'is-valid': !$v.model.$invalid }" placeholder="Model telefona"></b-form-textarea>
          <div class="valid-feedback">Your model input is valid</div>
          <div class="invalid-feedback">
            <span v-if="!$v.model.required">Polje model mora biti popunjeno</span>
            <span v-if="!$v.model.minLength">Morate imati najmanje 3 karaktera</span>
            <span v-if="!$v.model.minLength">Morate imati najvise 15 karaktera</span>
          </div>
        </b-col>
        <b-col sm="1">
          <b-button variant="primary" size="lg" @click="addNew">Sacuvaj</b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-container>
</template>

<script>
import { mapActions } from 'vuex';
import {required , minLength , maxLength } from 'vuelidate/lib/validators'
export default {
  name: "EditMessage",
  props: {
    MaTel: {
      type: String,
      default: ''
    },
    MoTel: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      marka: '',
      model: ''
    }
  },
  validations: {
   marka: {
    required,
     minLength: minLength(3),
     maxLength: maxLength(15)
   },
    model: {
      required,
      minLength: minLength(3),
      maxLength: maxLength(15)
    }
  },

  mounted: function () {
    this.marka = this.MaTel;
    this.model = this.MoTel;
  },
  methods: {
    ...mapActions(['new_message', 'change_message']),

    addNew: function() {
      const msg = JSON.stringify({markaTelefona: this.marka, modelTelefona: this.model});

      if (!this.$route.params.id)
        this.new_message(msg);
      else
        this.change_message({id: this.$route.params.id, msg: msg});

      this.marka = '';
      this.model = '';
    }
  }
}
</script>

